import * as React from 'react';
import { ContentPage } from '../../scripts/components';

      import { PageContent, Markdown } from '../../scripts/components';

      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/tutorials/04-Forms.md";
      const html = `
<h1 id="form-submission-by-example">Form Submission by Example</h1>
<h2 id="standard-forms">Standard Forms</h2>
<p>One of the most needed functionalities of a dynamic DOM is the possibility to submit forms. Submitting a form with AngleSharp can be as simple as:</p>
<pre><code class="hljs language-cs"><span class="hljs-keyword">var</span> context = BrowsingContext.New(Configuration.Default.WithDefaultLoader());
<span class="hljs-keyword">var</span> queryDocument = <span class="hljs-keyword">await</span> context.OpenAsync(<span class="hljs-string">&quot;https://google.com&quot;</span>);
<span class="hljs-keyword">var</span> form = queryDocument.QuerySelector&lt;IHtmlFormElement&gt;(<span class="hljs-string">&quot;form&quot;</span>);
<span class="hljs-keyword">var</span> resultDocument = <span class="hljs-keyword">await</span> form.SubmitAsync(<span class="hljs-keyword">new</span> { q = <span class="hljs-string">&quot;anglesharp&quot;</span> });
<span class="hljs-comment">// e.g., resultDocument.QuerySelectorAll&lt;IHtmlAnchorElement&gt;(&quot;#ires .g h3.r a&quot;).Select(m =&gt; m.Href).Dump();</span>
</code></pre>
<p>The shown example uses a special overload of the <code>SubmitAsync</code> method of an <code>IHtmlFormElement</code>, which allows us to pass in an anonymous object that consists of the names of the form fields with their desired values. Alternatively, we could have written code like</p>
<pre><code class="hljs language-cs"><span class="hljs-comment">// ...</span>
<span class="hljs-keyword">var</span> queryInput = form.Elements[<span class="hljs-string">&quot;q&quot;</span>] <span class="hljs-keyword">as</span> IHtmlInputElement;

<span class="hljs-keyword">if</span> (queryInput != <span class="hljs-literal">null</span>)
{
    queryInput.Value = <span class="hljs-string">&quot;anglesharp&quot;</span>;
}
</code></pre>
<p>The latter version is much more verbose and requires additional checking on our side. The upside of this version is that we can also treat the error case (i.e., the expected field with the name <code>q</code> is not found or is not an <code>IHtmlInputElement</code>), which is completely ignored in the former version.</p>
<p>An important aspect of submitting forms is that we require at least a default loader. Forms usually require making an HTTP request for loading a new document (mostly using the POST verb), which yields the demand for having a requester configured upfront. Note that sometimes forms even come with stronger requirements, especially if they are properly secured. In such cases the use of <code>WithCookies()</code> to configure a cookie container could be mandatory.</p>
<p>The code for using a cookie container looks almost identical:</p>
<pre><code class="hljs language-cs"><span class="hljs-keyword">var</span> config = Configuration.Default
    .WithDefaultLoader()
    .WithCookies();
<span class="hljs-keyword">var</span> context = BrowsingContext.New(config);
<span class="hljs-comment">// ...</span>
</code></pre>
<h2 id="ajax-%2F-javascript-powered-forms">AJAX / JavaScript Powered Forms</h2>
<p>AngleSharp itself does not provide a JavaScript parser or engine. Some libraries exist or try to solve this problem, however, at the moment no library is mature or powerful enough to cope with JavaScript powered forms in general.</p>
<p>Once a form is no longer a real form, but relies on JavaScript manipulation (or just uses a bunch of input fields to form a set of data that is ultimately transferred to a server via a constructed XHR / <code>fetch</code> call) AngleSharp is essentially out of the game. At this point one may try to reverse engineer the logic of the JavaScript to perform the same steps and submit the same data. AngleSharp may assist here, but is certainly not the greatest help, if any.</p>
`;
    

export default () => (
  <ContentPage>
    
      <PageContent meta={{"title":"Form Submissions","section":"General"}}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    
  </ContentPage>
);